import React, { Component } from 'react';
import { connect } from "react-redux";
import * as actions from '../actions';
import { Button } from 'primereact/button';
import GaugeForm from '../components/forms/GaugeForm';
import CylinderGaugeForm from '../components/forms/CylinderGaugeForm';
import SolidGaugeForm from '../components/forms/SolidGaugeForm';
import BasicGraphForm from '../components/forms/BasicGraphForm';
import TextBoxForm from '../components/forms/TextBoxForm';
import AdvancedGraphForm from '../components/forms/AdvancedGraphForm';
import MapForm from '../components/forms/MapForm';
import TableForm from '../components/forms/TableForm';
import CustomTableForm from '../components/forms/CustomTableForm';
import IframeTileForm from '../components/forms/IframeTileForm';

const forms = {
  Gauge: GaugeForm,
  CylinderGauge: CylinderGaugeForm,
  SolidGauge: SolidGaugeForm,
  BasicGraph: BasicGraphForm,
  TextBox: TextBoxForm,
  Map: MapForm,
  AdvancedGraph: AdvancedGraphForm,
  Table: TableForm,
  CustomTable: CustomTableForm,
  IframeTile: IframeTileForm
}

class Modal extends Component {
  state = {
    properties: {type: null}
  }

  handleData = (formData) => {
    const { tile } = this.props.ui.modal;
    this.setState({properties:{...this.state.properties,  type: tile.properties.type, ...formData}})
  }

  renderForm = () => {
    const { tile } = this.props.ui.modal;
    if(tile && tile.properties.type && forms[tile.properties.type.value]) {
      const Form = forms[tile.properties.type.value];
      return <Form addData={this.handleData}/>
    } else {
      return <div>No Type Selected</div>
    }
  }

  closeModal = () => {
    const { resetModal } = this.props;
    this.setState({properties: {type:null}});
    resetModal();
  }

  submitModal = () => {
    const { resetModal, ui, editTile, activeTab } = this.props;
    const { properties } = this.state;
    const newTile = {...ui.modal.tile, properties: {...properties}};
    if (properties.type){
      editTile(newTile, activeTab.index);
    }
    this.setState({properties: {type:null}});
    resetModal();
  }

  render () {
    const { ui } = this.props;
    const showHideClassName = `${ui.modal.visible ? 'modal-show' : 'modal-hide'}`;

    return (
      <div className={`background ${showHideClassName}`}>
        <section className={`modal`}>
          <div className='modal-header' >
            <h1 style={{margin: 0, fontSize: '1.25rem'}}>Edit Tile:</h1>
          </div>
          <div className='modal-main'>
            {this.renderForm()}
            <div className='modal-footer'>
              <Button label="Submit" icon="pi pi-check" onClick={this.submitModal} />
              <Button label="Close" icon="pi pi-times" onClick={this.closeModal} style={{marginLeft:'1rem', background: '#e53935', border: '1px solid #e53935'}} />
            </div>
          </div>
        </section>
      </div>
    )
  }
}

function mapStateToProps({ ui, activeTab }) {
  return { ui, activeTab }
}

export default connect(mapStateToProps, actions)(Modal);
